import { useEffect } from 'react';

//Context for the entire App
import { useAppContext } from './AppContext';

function setValues(appdispatch) {
  appdispatch({type : "WINDOWWIDTH_CHANGE", payload :  window.innerWidth});
  appdispatch({type : "WINDOWHEIGHT_CHANGE", payload :  window.innerHeight});

  if ( window.innerWidth < 768) {
    appdispatch({type : "DEVICETYPE_CHANGE", payload : 0});
  } else if ( window.innerWidth < 1024) {
    appdispatch({type : "DEVICETYPE_CHANGE", payload : 1});
  } else {
    appdispatch({type : "DEVICETYPE_CHANGE", payload : 2});
  }

  if ( window.innerWidth >  window.innerHeight) {
    appdispatch({type : "ISPORTRAIT_CHANGE", payload : false});
  } else {
    appdispatch({type : "ISPORTRAIT_CHANGE", payload : true});
  }
}

//component for screen resize
//https://blog.logrocket.com/developing-responsive-layouts-with-react-hooks/
const Viewport = () => {
  const { appdispatch } = useAppContext();

  useEffect(() => {
    setValues(appdispatch);

    const handleWindowResize = () => {
        setValues(appdispatch);
    }

    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, [appdispatch]);
}


export default Viewport;
