import React , {useEffect} from 'react';

import { useAppContext } from '../AppContext';

import { useBrandContext } from './BrandContext';

import BrandMessage from './BrandMessage';
import BrandInput from './BrandInput';
import BrandResult from './BrandResult';

import { makeStyles , useTheme } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Button from '@material-ui/core/Button';

//uk - design of material-ui components
const useStyles = makeStyles({
  brandStepper: {
    backgroundColor: "var(--brandstepper-bg-color)",
  },
  brandButton: {
  },
  brandProgress: {
  }
});

//uk - the navigation wrapper around all things brand
//uk - this is not stateful, rather it fetches data from context
const BrandNav = () => {
  //uk - here we are fetching the current context
  //uk - dispatch is the consumer, use it to call reducer in brand useContext
  //uk - state is the current state of context
  const { brandstate , branddispatch } = useBrandContext();
  const { appstate } = useAppContext();

  let brandNavStyle = {};
  brandNavStyle.marginTop = "var(--brandstepper-wrapper-div-margin-top)";

  let stepperStyle = {};
  stepperStyle.maxWidth = "var(--width-webwrapper-max)";

  let brandAppStyle = {};
  brandAppStyle.margin = "var(--brandapp-margin-tb) var(--brandapp-margin-lr)";

  //uk - these are the pages to be conditionally displayed
  //uk - index defines the flow, (not necessariy though), so this has to be sequential
  const steps = [<BrandInput pg={1} />, <BrandInput pg={2} />, <BrandInput pg={3} />, <BrandResult />];

  //uk - handle next clicks here
  function handleNextClick() {
    branddispatch({type : "CURRSTEP_CHANGE", payload :  brandstate.currStep + 1});
  }

  //uk - handle back clicks here
  function handlePrevClick() {
    branddispatch({type : "CURRSTEP_CHANGE", payload :  brandstate.currStep - 1});
  }

  const classes = useStyles();
  const theme = useTheme();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [brandstate.currStep]);

  //uk - this is what will be displayed on the screen
  return (
    <div>
      { !appstate.openMenu &&
        <section className="ukcntapp ukcntint">

          {appstate.showHint && <BrandMessage screen={brandstate.currStep} />}

          <section style={brandAppStyle} className="ukcntapp">
            {steps[brandstate.currStep]}
            <section style={brandNavStyle} className="ukcntapp">
              <section style={stepperStyle} className="ukcntapp">
                <MobileStepper
                  variant="dots"
                  steps={steps.length}
                  position="static"
                  activeStep={brandstate.currStep}
                  className={classes.brandStepper}

                  nextButton={
                    <Button
                      className={classes.brandButton}
                      size="small"
                      onClick={e => {e.preventDefault(); handleNextClick();}}
                      disabled={brandstate.currStep === steps.length-1}>
                        <h5>Next</h5>
                    </Button>}
                  backButton={
                    <Button
                      size="small"
                      className={classes.brandButton}
                      onClick={e => { e.preventDefault(); handlePrevClick();}}
                      disabled={brandstate.currStep === 0}>
                        <h5>Back</h5>
                    </Button>}
                  />
                </section>

            </section>
          </section>
        </section>
      }
    </div>
  );
}

export default BrandNav;
