import React from 'react';
import './MenuIcon.css';
import { useAppContext } from './AppContext';

const MenuIcon = () => {
  const { appdispatch } = useAppContext ();

  const openMenu = () => {
    appdispatch({type : "OPENMENU_CHANGE", payload : true});
  }

  return (
    <div>
        <section className="menuIcon" id="menuIcon1" onClick={openMenu}>
          <div className="menuDot" id="menuDot1"></div>
          <div className="menuDot" id="menuDot2"></div>
          <div className="menuDot" id="menuDot3"></div>
          <div className="menuDot" id="menuDot4"></div>
          <div className="menuDot" id="menuDot5"></div>
          <div className="menuDot" id="menuDot6"></div>
          <div className="menuDot" id="menuDot7"></div>
          <div className="menuDot" id="menuDot8"></div>
          <div className="menuDot" id="menuDot9"></div>
        </section>
    </div>
  );
}

export default MenuIcon;
