import React from 'react';

import BrandInputSlider from './BrandInputSlider';

const BrandInput = (props) => {
  let questionnaire = "";
  let question = "";

  switch(props.pg) {
    case 1:
      questionnaire =
        <div>
          <BrandInputSlider id = 'slider1' />
          <BrandInputSlider id = 'slider2' />
        </div>
      question =
        <h3>Brand Appearance</h3>
      break;

    case 2:
      questionnaire =
        <div>
          <BrandInputSlider id = 'slider3' />
          <BrandInputSlider id = 'slider4' />
        </div>
      question =
        <h3>Brand Appearance</h3>
      break;

    case 3:
      questionnaire =
        <div>
          <BrandInputSlider id = 'slider5' />
          <BrandInputSlider id = 'slider6' />
        </div>
      question =
        <h3>Target Audience</h3>
      break;

    default:
      throw new Error(
          "Page must have a valid number" + props.pg
      );
  }

  let quesStyle = {};
  quesStyle.backgroundColor = "var(--questionnaire-header-bg-color)";
  quesStyle.borderRadius = "var(--questionnaire-header-border-radius)";
  quesStyle.height = "var(--questionnaire-header-height)";
  quesStyle.display = "flex";
  quesStyle.justifyContent = "center";
  quesStyle.alignItems = "center";
  quesStyle.gridColumnStart = 1;
  quesStyle.gridColumnEnd = 3;

  let questionnaireWrapperStyle = {};
  questionnaireWrapperStyle.display = "grid";
  questionnaireWrapperStyle.gridTemplateColumns = "var(--questionnaire-wrapper-grid-col)";

  return (
    <div>
    <article style={quesStyle} className="ukcntapp">
      {question}
    </article>
    <section style={questionnaireWrapperStyle} className="ukcntapp">
      <div></div>
      <div>
        <>
          {questionnaire}
        </>
      </div>
      <div></div>
    </section>
    </div>
  );
}

export default BrandInput;
