import React, {useState} from 'react';
import { useBrandContext } from './BrandContext';

const PaletteDetail = () => {
  const { brandstate , branddispatch } = useBrandContext();

  const handleClick1 = () => {
    branddispatch({type : "DETAILVALUE_CHANGE", payload : 1});
  }

  const handleClick2 = () => {
    branddispatch({type : "DETAILVALUE_CHANGE", payload : 2});
  }

  const handleClick3 = () => {
    branddispatch({type : "DETAILVALUE_CHANGE", payload : 3});
  }

  const handleClickClose = () => {
    branddispatch({type : "CLOSEDETAIL_CHANGE", payload : true});
  }

  let detailWrapperStyle = {};
  let detailMenuStyle = {};
  let detailButtonStyle = {};
  let detailBigButtonStyle = {};
  let detailTextStyle = {};
  let detailStyle = {};
  let bottomButtonStyle = {};
  let saveButtonStyle = {};

  /*Sample1*/
  let divlmSytle = {};
  let divllSytle = {};
  let divldSytle = {};
  let divdmSytle = {};
  let divdlSytle = {};
  let divddSytle = {};

  let s1textldStyle = {};
  let s1textlmStyle = {};
  let s1textllStyle = {};
  let s1textddStyle = {};
  let s1textdmStyle = {};
  let s1textdlStyle = {};
  /*end of Sample1*/

  /*Sample2*/
  let svglmStyle = {};
  let svgllStyle = {};
  let svgldStyle = {};
  let svgdmStyle = {};
  let svgdlStyle = {};
  let svgddStyle = {};
  let imgStyle = {};
  let imgTextStyle = {};
  let imgDivStyle = {}
  /*end of Sample2*/

  /*accent_light*/
  let light_main ="";
  let light_main_purpose ="";
  let light_main_text ="";
  let light_main_name ="";
  /*bg_light*/
  let light_light ="";
  let light_light_purpose ="";
  let light_light_text ="";
  let light_light_name ="";
  /*dark_accent_light*/
  let light_dark ="";
  let light_dark_purpose ="";
  let light_dark_text ="";
  let light_dark_name ="";
  /*accent_dark*/
  let dark_main ="";
  let dark_main_purpose ="";
  let dark_main_text ="";
  let dark_main_name ="";
  /*bg_dark*/
  let dark_light ="";
  let dark_light_purpose ="";
  let dark_light_text ="";
  let dark_light_name ="";
  /*text*/
  let dark_dark ="";
  let dark_dark_purpose ="";
  let dark_dark_text ="";
  let dark_dark_name ="";

  try {
    light_main = brandstate.palettes[brandstate.palette]['col_main_light']['col_main']['hex'];
    light_main_purpose = brandstate.palettes[brandstate.palette]['col_main_light']['col_main']['purpose'];
    light_main_text = brandstate.palettes[brandstate.palette]['col_main_light']['col_main']['font'];
    light_main_name = brandstate.palettes[brandstate.palette]['col_main_light']['col_main']['name'];

    light_light = brandstate.palettes[brandstate.palette]['col_main_light']['col_light1']['hex'];
    light_light_purpose = brandstate.palettes[brandstate.palette]['col_main_light']['col_light1']['purpose'];
    light_light_text = brandstate.palettes[brandstate.palette]['col_main_light']['col_light1']['font'];
    light_light_name = brandstate.palettes[brandstate.palette]['col_main_light']['col_light1']['name'];

    light_dark = brandstate.palettes[brandstate.palette]['col_main_light']['col_dark1']['hex'];
    light_dark_purpose = brandstate.palettes[brandstate.palette]['col_main_light']['col_dark1']['purpose'];
    light_dark_text = brandstate.palettes[brandstate.palette]['col_main_light']['col_dark1']['font'];
    light_dark_name = brandstate.palettes[brandstate.palette]['col_main_light']['col_dark1']['name'];

    dark_main = brandstate.palettes[brandstate.palette]['col_main_dark']['col_main']['hex'];
    dark_main_purpose = brandstate.palettes[brandstate.palette]['col_main_dark']['col_main']['purpose'];
    dark_main_text = brandstate.palettes[brandstate.palette]['col_main_dark']['col_main']['font'];
    dark_main_name = brandstate.palettes[brandstate.palette]['col_main_dark']['col_main']['name'];

    dark_light = brandstate.palettes[brandstate.palette]['col_main_dark']['col_light1']['hex'];
    dark_light_purpose = brandstate.palettes[brandstate.palette]['col_main_dark']['col_light1']['purpose'];
    dark_light_text = brandstate.palettes[brandstate.palette]['col_main_dark']['col_light1']['font'];
    dark_light_name = brandstate.palettes[brandstate.palette]['col_main_dark']['col_light1']['name'];

    dark_dark = brandstate.palettes[brandstate.palette]['col_main_dark']['col_dark1']['hex'];
    dark_dark_purpose = brandstate.palettes[brandstate.palette]['col_main_dark']['col_dark1']['purpose'];
    dark_dark_text = brandstate.palettes[brandstate.palette]['col_main_dark']['col_dark1']['font'];
    dark_dark_name = brandstate.palettes[brandstate.palette]['col_main_dark']['col_dark1']['name'];

  } catch (err) {
    console.log("ignore");
  }

  /*detailWrapperStyle.border = "2px solid "+dark_dark;*/
  detailWrapperStyle.width = "280px";
  detailWrapperStyle.height = "430px";
  detailWrapperStyle.position = "sticky";
  detailWrapperStyle.top = "100px";
  detailWrapperStyle.marginBottom = "20px";
  detailWrapperStyle.opacity = 1;
  detailWrapperStyle.boxShadow= "var(--menu-button-shadow-xy) var(--menu-button-shadow-xy) var(--menu-button-shadow-merge) var(--menu-button-shadow-start) var(--menu-button-shadow-color)";

  detailMenuStyle.display = "flex";
  detailMenuStyle.flexDirection = "row";
  detailMenuStyle.justifyContent = "space-between";

  detailButtonStyle.backgroundColor = dark_main;
  detailButtonStyle.width = 92;
  detailButtonStyle.cursor = "pointer";
  detailButtonStyle.textAlign = "center";
  detailButtonStyle.borderRadius = "5px 5px 0px 0px";

  detailBigButtonStyle.backgroundColor = dark_dark;
  detailBigButtonStyle.width = 92;
  detailBigButtonStyle.cursor = "pointer";
  detailBigButtonStyle.textAlign = "center";
  detailBigButtonStyle.borderRadius = "5px 5px 0px 0px";

  detailTextStyle.color = "var(--white)";

  bottomButtonStyle.display = "flex";
  bottomButtonStyle.flexDirection = "row";
  bottomButtonStyle.justifyContent = "space-between";
  bottomButtonStyle. paddingTop = 2;

  saveButtonStyle.backgroundColor = dark_main;
  saveButtonStyle.width = 92;
  saveButtonStyle.lineHeight = 35;
  saveButtonStyle.cursor = "pointer";
  saveButtonStyle.textAlign = "center";
  saveButtonStyle.borderRadius = 5;

  /*Sample1*/
  divlmSytle.backgroundColor = light_main;
  divllSytle.backgroundColor = light_light;
  divldSytle.backgroundColor = light_dark;
  divdmSytle.backgroundColor = dark_main;
  divdlSytle.backgroundColor = dark_light;
  divddSytle.backgroundColor = dark_dark;

  divlmSytle.width = 280;
  divlmSytle.height = 94;
  divlmSytle.display="flex";
  divlmSytle.flexDirection="column";
  divlmSytle.justifyContent="center";
  divlmSytle.alignItems="center";

  divllSytle.width = 280;
  divllSytle.height = 47;
  divllSytle.display="flex";
  divllSytle.flexDirection="column";
  divllSytle.justifyContent="center";
  divllSytle.alignItems="center";

  divldSytle.width = 280;
  divldSytle.height = 47;
  divldSytle.display="flex";
  divldSytle.flexDirection="column";
  divldSytle.justifyContent="center";
  divldSytle.alignItems="center";

  divdmSytle.width = 280;
  divdmSytle.height = 94;
  divdmSytle.display="flex";
  divdmSytle.flexDirection="column";
  divdmSytle.justifyContent="center";
  divdmSytle.alignItems="center";

  divdlSytle.width = 280;
  divdlSytle.height = 47;
  divdlSytle.display="flex";
  divdlSytle.flexDirection="column";
  divdlSytle.justifyContent="center";
  divdlSytle.alignItems="center";

  divddSytle.width = 280;
  divddSytle.height = 47;
  divddSytle.display="flex";
  divddSytle.flexDirection="column";
  divddSytle.justifyContent="center";
  divddSytle.alignItems="center";


  s1textldStyle.color=light_dark_text;
  s1textlmStyle.color=light_main_text;
  s1textllStyle.color=light_light_text;
  s1textddStyle.color=dark_dark_text;
  s1textdmStyle.color=dark_main_text;
  s1textdlStyle.color=dark_light_text;

  /*end of Sample1*/

  /*Sample2*/
  svglmStyle.fill = light_main;
  svgllStyle.fill = light_light;
  svgldStyle.fill = light_dark;
  svgdmStyle.fill = dark_main;
  svgdlStyle.fill = dark_light;
  svgddStyle.fill = dark_dark;

  imgDivStyle.position = "absolute";
  imgDivStyle.top = 25;
  imgDivStyle.left = 0;
  imgDivStyle.height = 375;
  imgDivStyle.width = 280;
  imgDivStyle.display = "flex";
  imgDivStyle.flexDirection = "column";
  imgDivStyle.justifyContent = "center";
  imgDivStyle.alignItems = "center";

  imgStyle.width = 200;
  imgTextStyle.color = "var(--white)";
  /*end of Sample2*/

  return (
    <div>
      <section style={detailWrapperStyle} className="ukcntapp">
        <section style={detailMenuStyle}>
          {!(brandstate.detailValue === 1) && <div style={detailButtonStyle} onClick={handleClick1}>
            <p style={detailTextStyle}>Sample 1</p>
          </div>}
          {(brandstate.detailValue === 1) && <div style={detailBigButtonStyle}>
            <p style={detailTextStyle}>Sample 1</p>
          </div>}
          {!(brandstate.detailValue === 2) && <div style={detailButtonStyle} onClick={handleClick2}>
            <p style={detailTextStyle}>Sample 2</p>
          </div>}
          {(brandstate.detailValue === 2) && <div style={detailBigButtonStyle}>
            <p style={detailTextStyle}>Sample 2</p>
          </div>}
          {!(brandstate.detailValue === 3) && <div style={detailButtonStyle} onClick={handleClick3}>
            <p style={detailTextStyle}>Sample 3</p>
          </div>}
          {(brandstate.detailValue === 3) && <div style={detailBigButtonStyle}>
            <p style={detailTextStyle}>Sample 3</p>
          </div>}
        </section>

        {(brandstate.detailValue === 1) && <section style={detailStyle} className="ukcntapp detail one">
          <div style={divldSytle}><p style={s1textldStyle}>{light_dark_purpose}({light_dark})</p></div>
          <div style={divlmSytle}><p style={s1textlmStyle}>{light_main_purpose}({light_main})</p><p style={s1textlmStyle}>{light_main_name}</p></div>
          <div style={divllSytle}><p style={s1textllStyle}>{light_light_purpose}({light_light})</p></div>
          <div style={divdlSytle}><p style={s1textdlStyle}>{dark_light_purpose}({dark_light})</p></div>
          <div style={divdmSytle}><p style={s1textdmStyle}>{dark_main_purpose}({dark_main})</p><p  style={s1textdmStyle}>{dark_main_name}</p></div>
          <div style={divddSytle}><p style={s1textddStyle}>{dark_dark_purpose}({dark_dark})</p></div>
        </section>}
        {(brandstate.detailValue === 2) && <section style={detailStyle} className="ukcntapp detail two">
          <svg height="375" width="280">
            <polygon points="140,200 100,0 280,0 280,50" style={svgllStyle} />{/*light_light*/}
            <polygon points="140,200 280,50 280,300" style={svgdlStyle} />{/*dark_light*/}
            <polygon points="140,200 280,300 280,375 175,375" style={svglmStyle} />{/*light_main*/}
            <polygon points="140,200 175,375 0,375 0,350" style={svgdmStyle} />{/*dark_main*/}
            <polygon points="140,200 0,350 0,100" style={svgllStyle} />{/*light_light*/}
            <polygon points="140,200 0,100 0,0 100,0" style={svgdlStyle} />{/*dark_light*/}
          </svg>
          <div style={imgDivStyle}>
            <img src={require("./success.png")} alt="Success Kid" style={imgStyle} />
            <h3 style={imgTextStyle}>Success Kid</h3>
          </div>
        </section>}
        {(brandstate.detailValue === 3) && <section style={detailStyle} className="ukcntapp detail three">

        </section>}
        <section style={bottomButtonStyle}>
          <div style={saveButtonStyle}>
            <p style={detailTextStyle}>Save</p>
          </div>
          <div style={saveButtonStyle}>
            <p style={detailTextStyle}>Shortlist</p>
          </div>
          <div style={saveButtonStyle} onClick={handleClickClose}>
            <p style={detailTextStyle}>Close</p>
          </div>
        </section>
      </section>
    </div>
  );
}

export default PaletteDetail;
