import React from 'react';
import { Link } from 'react-router-dom';

//menu cards
import MenuCard from './MenuCard';

const Menu = () => {
  let menuStyle = {};
  menuStyle.display= "flex";
  menuStyle.flexDirection= "row";
  menuStyle.flexWrap= "wrap";
  menuStyle.justifyContent= "center";

  return (
    <nav className="uncntapp" style={menuStyle}>
      <Link to="/branding">
        <MenuCard type="BRAND" />
      </Link>
      <Link to="/posts">
        <MenuCard type="POSTS" />
      </Link>
      <Link to="/videos">
        <MenuCard type="VIDEOS" />
      </Link>
    </nav>

  );
}

export default Menu;
