import React, { createContext, useReducer, useContext } from "react";

//uk - creating context for brand
//uk - BrandContext got two objects provider and consumer
const BrandContext = createContext();

const defaultPalettes = "";

//uk - initial state of context
//uk - this will be set to context when provider is called from outside
const initialState = {
  currStep  : 0,
  slider1 : 50,
  slider2 : 50,
  slider3 : 50,
  slider4 : 50,
  slider5 : 50,
  slider6 : [25,55],
  palette : '',
  palettes : defaultPalettes,
  closeDetail : true,
  detailValue : 1
};

//uk - wrapper for useReducer, take two inputs - current state and action and returns new state
//uk - actions on context
const theReducer = (state, action) => {
  switch (action.type) {
    case "CURRSTEP_CHANGE":
      return{...state, currStep : action.payload };
    case "SLIDER1_CHANGE":
      return{...state, slider1 : action.payload };
    case "SLIDER2_CHANGE":
      return{...state, slider2 : action.payload };
    case "SLIDER3_CHANGE":
      return{...state, slider3 : action.payload };
    case "SLIDER4_CHANGE":
      return{...state, slider4 : action.payload };
    case "SLIDER5_CHANGE":
      return{...state, slider5 : action.payload };
    case "SLIDER6_CHANGE":
      return{...state, slider6 : action.payload };
    case "PALETTE_CHANGE":
      return{...state, palette : action.payload };
    case "PALETTES_CHANGE":
      return{...state, palettes : action.payload };
    case "CLOSEDETAIL_CHANGE":
      return{...state, closeDetail : action.payload };
    case "DETAILVALUE_CHANGE":
      return{...state, detailValue : action.payload };
    default:
      throw new Error(
          "BrandReducer must be dispatched a valid action "+action.type
      );
  }
}

//uk - initializing the context and provider
//uk -  for first run it returns initialstate
export const BrandProvider = function({ children }) {
  const [brandstate, branddispatch] = useReducer(theReducer, initialState);

  return (
    <BrandContext.Provider value={{ brandstate, branddispatch }}>
      {children}
    </BrandContext.Provider>
  );
};

// uk - wrapper around useContext for a better error message
export const useBrandContext = () => {
  const context = useContext(BrandContext);

  if (context === undefined) {
    throw new Error(
      "useBrandContext must be used within a BrandProvider"
    );
  }
  return context;
}
