import React from 'react';
import { Switch, Route } from 'react-router-dom';

//place holder component for features under construction
import UnderConstruct from './UnderConstruct';

//placeholder for emptyness
import EmptyPage from './EmptyPage';

//component for branding tool
import BrandNav from './Brand/BrandNav';
import {BrandProvider} from './Brand/BrandContext';

const MenuRoutes = () => {
  return (
    <Switch>
      <Route path="/branding">
        <BrandProvider>
          <BrandNav />
        </BrandProvider>
      </Route>
      <Route path="/posts">
        <UnderConstruct />
      </Route>
      <Route path="/videos">
        <UnderConstruct />
      </Route>
      <Route path="/">
        <EmptyPage />
      </Route>
    </Switch>
  );
}

export default MenuRoutes;
