import React, { createContext, useReducer, useContext } from "react";

//uk - creating context for app
//uk - AContext got two objects provider and consumer
const AppContext = createContext();

//uk - initial state of context
//uk - this will be set to context when provider is called from outside
const initialState = {
  windowWidth : 375,
  windowHeight : 768,
  deviceType : 0, //0-mobile(<768), 1-table(<1024), 2-desktop
  isPortrait : true,
  isFirstMenu : true,
  openMenu : true,
  showHint : true,
  closeHint : true
};

//uk - wrapper for useReducer, take two inputs - current state and action and returns new state
//uk - actions on context
const theReducer = (appstate, action) => {
  switch (action.type) {
    case "WINDOWWIDTH_CHANGE":
      return{...appstate, windowWidth : action.payload };
    case "WINDOWHEIGHT_CHANGE":
      return{...appstate, windowHeight : action.payload };
    case "DEVICETYPE_CHANGE":
      return{...appstate, deviceType : action.payload };
    case "ISPORTRAIT_CHANGE":
      return{...appstate, isPortrait : action.payload };
    case "OPENMENU_CHANGE":
      return{...appstate, openMenu : action.payload };
    case "ISFIRSTMENU_CHANGE":
      return{...appstate, isFirstMenu : action.payload };
    //case "OPENINTERACT_CHANGE":
      //return{...appstate, openInteract : action.payload };
    case "SHOWHINT_CHANGE":
      return{...appstate, showHint : action.payload };
    case "CLOSEHINT_CHANGE":
      return{...appstate, closeHint : action.payload };
    default:
      throw new Error(
          "AppReducer must be dispatched a valid action"
      );
  }
}

//uk - initializing the context and provider
//uk -  for first run it returns initialstate
export const AppProvider = function({ children }) {
  const [appstate, appdispatch] = useReducer(theReducer, initialState);

  return (
    <AppContext.Provider value={{ appstate, appdispatch }}>
      {children}
    </AppContext.Provider>
  );
};

// uk - wrapper around useContext for a better error message
export const useAppContext = () => {
  const context = useContext(AppContext);

  if (context === undefined) {
    throw new Error(
      "useAppContext must be used within a AppProvider"
    );
  }
  return context;
}
