import React, { useEffect } from 'react';
import './BrandResult.css';
import PaletteDetail from './BrandPaletteDetail';
import { useAppContext } from '../AppContext';
import { useBrandContext } from './BrandContext';
import axios from "axios";

const PaletteDetailMobile = () => {
  let mobilePaletteDetailStyle = {};
  mobilePaletteDetailStyle.position = "absolute";
  mobilePaletteDetailStyle.left = "-60px";
  mobilePaletteDetailStyle.width = "var(--app-width)";
  mobilePaletteDetailStyle.height = "var(--app-min-height)";
  mobilePaletteDetailStyle.backgroundColor = "var(--white)";
  mobilePaletteDetailStyle.opacity = 0.7;

  let mobilePaletteDetailStyleii = {};
  mobilePaletteDetailStyleii.position = "absolute";
  mobilePaletteDetailStyleii.left = "-60px";
  mobilePaletteDetailStyleii.width = "var(--app-width)";
  mobilePaletteDetailStyleii.height = "var(--app-min-height)";
  mobilePaletteDetailStyleii.display = "flex";
  mobilePaletteDetailStyleii.flexDirection = "column";
  mobilePaletteDetailStyleii.justifyContent = "center";
  mobilePaletteDetailStyleii.alignItems = "center";

  return (
    <div>
      <section style={mobilePaletteDetailStyle} className="ukcntapp">
      </section>
      <section style={mobilePaletteDetailStyleii} className="ukcntapp">
        <PaletteDetail />
      </section>
    </div>
  );
}

const LoadingMessage = () => {
  return (
    <section className="textwrapper ukcntapp" id="textwrapper1">
      <article className="brandText ukcntapp" id="brandText1">
        <p>
          We are fetching the color palettes for you, will be back in a jiffy!
        </p>
      </article>
    </section>
  );
}

//uk - color for displaying one color in color engine results
//uk - color is a stateless function
const PaletteColor =  ({colorStyle, textStyle, color}) => {
  return(
    <div className="colorContainer" style = {colorStyle}>
      <div className = "colorName">
        <p style = {textStyle} >{color}</p>
      </div>
    </div>
  );
}

//uk - palette for displaying one color engine results
//uk - palette is a stateless function
const BrandPalette = ({id , palette}) => {
  const { branddispatch } = useBrandContext();
  var colorArr = [];

  function handlePaletteClick() {
    branddispatch({type : 'PALETTE_CHANGE', payload : id});
    branddispatch({type : 'CLOSEDETAIL_CHANGE', payload : false});
  }

  const col1_iterator = ['col_dark1','col_main','col_light1'];
  const col2_iterator = ['col_light1','col_main','col_dark1'];
  //const col_type_iterator = ['col_main_light','col_main_dark'];
  let colType = '';
  var color = '';
  var style = {};
  var textStyle = {};

  for (color in col1_iterator) {
    style = {};
    textStyle = {};
    colType='col_main_light';
    style.fontSize = "var(--result-sec-color-font)";
    style.letterSpacing = "var(--result-sec-space-font)";
    style.textAlign = "center";
    style.paddingTop = "var(--result-color-font-top-padding)";
    style.backgroundColor = palette[colType][col1_iterator[color]]['hex'];
    style.flexGrow = 1;
    textStyle.color = palette[colType][col1_iterator[color]]['font'];

    colorArr.push(
      <PaletteColor
        key= {palette[colType][col1_iterator[color]]['hex']}
        colorStyle= {style}
        textStyle= {textStyle}
        color= {palette[colType][col1_iterator[color]]['hex']}
    />);
  }

  for (color in col2_iterator) {
    style = {};
    textStyle = {};
    colType='col_main_dark';
    style.fontSize = "var(--result-sec-color-font)";
    style.letterSpacing = "var(--result-sec-space-font)";
    style.textAlign = "center";
    style.paddingTop = "var(--result-color-font-top-padding)";
    style.backgroundColor = palette[colType][col2_iterator[color]]['hex'];
    style.flexGrow = 1;
    textStyle.color = palette[colType][col2_iterator[color]]['font'];

    colorArr.push(
      <PaletteColor
        key= {palette[colType][col2_iterator[color]]['hex']}
        colorStyle= {style}
        textStyle= {textStyle}
        color= {palette[colType][col2_iterator[color]]['hex']}
    />);
  }

  let paletteStyle = {};
  paletteStyle.padding = "var(--palette-padding-tb) var(--palette-padding-lr)";
  paletteStyle.margin = "var(--palette-margin-tb) var(--palette-margin-lr)";
  paletteStyle.display = "flex";
  paletteStyle.flexDirection = "row";
  paletteStyle.boxShadow = "-3px 0px 15px -5px var(--col-accent1-dark)";


  return (
    <div style={paletteStyle} className="ukcntapp paletteContainer" onClick={handlePaletteClick}>
      {colorArr}
    </div>
  );
}

const BrandPalettes = (palettes) => {
  const { brandstate } = useBrandContext();
  const { appstate } = useAppContext();

  let nonTouchscreen = false;
  if(appstate.deviceType>1) nonTouchscreen = true;

  var paletteArr= [];
  for (var key in palettes['palettes']) {
    paletteArr.push(<BrandPalette key={key} palette={palettes['palettes'][key]} id={key} />);
  };

  let resultStyle = {};
  resultStyle.display = "grid";
  resultStyle.gridTemplateColumns = "var(--result-wrapper-grid-template)";

  let palettesStyle = {};
  palettesStyle.display = "grid";
  palettesStyle.gridTemplateColumns = "var(--palettes-grid-template)";
  palettesStyle.overflow = "hidden";

  let paletteDetStyle = {};
  paletteDetStyle.position = "var(--palette-detail-position)";
  paletteDetStyle.display = "flex";
  paletteDetStyle.flexDirection = "row";
  paletteDetStyle.justifyContent = "center";


  return(
    <div style={resultStyle} className="ukcntapp">
      <div style={palettesStyle} className="ukcntapp">
          {paletteArr}
      </div>
      <div style={paletteDetStyle}>
        {!brandstate.closeDetail && nonTouchscreen && <PaletteDetail />}
        {!brandstate.closeDetail && !nonTouchscreen && <PaletteDetailMobile />}
      </div>
    </div>
  );
}

const BrandResult = () => {
  const url = 'https://inmv2dvkc5.execute-api.ap-southeast-2.amazonaws.com/DEV/create';
  const { brandstate , branddispatch } = useBrandContext();

  let request = {};
  for (var key in brandstate) {
    if (key.includes('slider')) {
      request[key]=brandstate[key];
    }
  };

  useEffect(() => {
    branddispatch({type : "PALETTES_CHANGE", payload : ''});
    axios.post(url,{request})
     .then(response => {
       branddispatch({type : "PALETTES_CHANGE", payload : response.data});
     })
     .catch(error => {
       console.log(error);
     });
   },[branddispatch]);

  return(
    <div>
    {brandstate.palettes ? (
      <BrandPalettes palettes= {brandstate.palettes} />
      ) : (
      <LoadingMessage />
    )}
    </div>
  );
}

export default BrandResult;
