import React from 'react';
import { useBrandContext } from './BrandContext';

//import { makeStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';

const marks1 = [
  { value: 0, label: 'Calm', highlight: 'Calm, Collected, Subdued' },
  //{ value: 20, label: 'Balanced', highlight: 'Neither too Calm nor too Loud' },
  { value: 100, label: 'Loud', highlight: 'Loud, Fast-Paced, Energetic' }
];

const marks2 = [
  { value: 100, label: 'Playful', highlight: 'Innovative, Creative' },
  //{ value: 20, label: 'Balanced', highlight: 'Neither too Innovative nor too Practicle' },
  { value: 0, label: 'Serious', highlight: 'Practivle, Tangile' }
];

const marks3 = [
  { value: 0, label: 'Logical', highlight: 'Logical, Objective' },
  //{ value: 20, label: 'Balanced', highlight: 'Neither too Logical nor too Compassionate' },
  { value: 100, label: 'Compassionate', highlight: 'Compassionate, Caring' }
];

const marks4 = [
  { value: 100, label: 'Luxurious', highlight: 'Luxurious, Expensive' },
  //{ value: 20, label: 'Balanced', highlight: 'Neither too Luxirious nor too Affordable' },
  { value: 0, label: 'Affordable', highlight: 'Affordable, Cheap' }
];

const marks5 = [
  { value: 0, label: 'Female', highlight: 'Female or Feminine persona' },
  //{ value: 20, label: 'Neutral', highlight: 'Neither too Feminine nor too Masculine' },
  { value: 100, label: 'Male', highlight: 'Male or Masculine' }
];

const marks6 = [
  { value: 25, label: '25' },
  { value: 35, label: '35' },
  { value: 45, label: '45' },
  { value: 55, label: '55' },
  { value: 65, label: '65' },
];

//uk - slider for taking in inputs
//uk - use brandcontext as state
const BrandInputSlider = ({id}) => {
  const { brandstate , branddispatch } = useBrandContext();

  let step = 1;
  let marks = [];
  let min = 0;
  let max = 100;
  let message = "";
  let track = false;
  let iconBegin = "";
  let iconEnd = "";

  switch (id) {
    case 'slider1':
      step = 1;
      marks = marks1;
      min = 0;
      max = 100;
      message = 'MIND';
      track = false;
      iconBegin = require("./icon_calm.png");
      iconEnd = require("./icon_loud.png");
      break;

    case 'slider2':
      step = 1;
      marks = marks2;
      min = 0;
      max = 100;
      message = 'APPROACH';
      track = false;
      iconBegin = require("./icon_serious.png");
      iconEnd = require("./icon_playful.png");
      break;

    case 'slider3':
      step = 1;
      marks = marks3;
      min = 0;
      max = 100;
      message = 'NATURE';
      track = false;
      iconBegin = require("./icon_logical.png");
      iconEnd = require("./icon_compassionate.png");
      break;

    case 'slider4':
      step = 1;
      marks = marks4;
      min = 0;
      max = 100;
      message = 'PRICE';
      track = false;
      iconBegin = require("./icon_cheap.png");
      iconEnd = require("./icon_expensive.png");
      break;

    case 'slider5':
      step = 1;
      marks = marks5;
      min = 0;
      max = 100;
      message = 'GENDER';
      track = false;
      iconBegin = require("./icon_female.png");
      iconEnd = require("./icon_male.png");
      break;

    case 'slider6':
      step = 1;
      marks = marks6;
      min = 18;
      max = 70;
      message = 'AGE RANGE';
      track = 'normal';
      iconBegin = require("./icon_young.png");
      iconEnd = require("./icon_mature.png");
      break;

    default:
      throw new Error(
          "Sliders must have a valid action" + id
      );
  };

  const handleChange = (event,newValue) => {
    branddispatch({type : (id.toUpperCase()).concat("_CHANGE"), payload : newValue});
  };

  let sliderContainerStyle = {};
  sliderContainerStyle.margin = "var(--questionnaire-slider-container-margin-tb) 0";
  sliderContainerStyle.minWidth = "var(--questionnaire-slider-container-min-width)";

  let messageStyle = {};
  messageStyle.textAlign = "center";
  messageStyle.padding = "var(--questionnaire-message-padding-tb) var(--questionnaire-message-padding-lr)";

  let imgStyle = {};
  imgStyle.width = "var(--questionnaire-logo-width)";
  imgStyle.height = "var(--questionnaire-logo-height)";
  imgStyle.margin = "var(--questionnaire-logo-margin-tb) var(--questionnaire-logo-margin-lr)";
  imgStyle.display = "var(--questionnaire-logo-display)";

  let sliderDivStyle = {};
  sliderDivStyle.display = "flex";
  sliderDivStyle.flexDirection = "row";

  return (
    <section style={sliderContainerStyle} className="ukcntapp">
      <article style={messageStyle} className="ukcntapp">
      <h4>{message}</h4>
      </article>

      <div className="ukcntapp" style={sliderDivStyle}>
        <img src={iconBegin} alt="" style={imgStyle} />
        <Slider
          valueLabelDisplay="off"
          value={brandstate[id]}
          id={id}
          step={step}
          marks={marks}
          min={min}
          max={max}
          track={track}
          onChange={handleChange}
        />
        <img src={iconEnd} alt="" style={imgStyle} />
      </div>

    </section>
  )
}

export default BrandInputSlider;
