import React from 'react';
import './UnderConstruct.css';
import { useAppContext } from './AppContext';

const UnderConstruct = () => {
  const { appstate } = useAppContext();

  return (
    <div>
      {!appstate.openMenu &&
        <section className="ukcntapp" id="construct1">
          <section className="ukcntapp" id="construct2">
            <p>CONSTRUCTION ZONE</p>
          </section>
        </section>}
    </div>
  );
}

export default UnderConstruct;
