import React , {useEffect} from 'react';
import { useAppContext } from '../AppContext';


import { Checkbox } from '@material-ui/core';
import { FormControlLabel } from '@material-ui/core';
import { Button } from '@material-ui/core';

const BrandMessage = (props) => {
  const { appstate , appdispatch } = useAppContext();

  let message = "";
  let displayMessage = false;

  switch (props.screen) {
    case 0 :
      displayMessage = true;
      message =
        <div>
          <h3>Your personal profile</h3>
          <br/><p>Creating it is easy -
          <br/>1. Answer a few questions.
          <br/>2. Choose your colors.
          <br/>3. Thats it!
          <br/><br/></p>
        </div>
      break;

      case 3 :
        displayMessage = true;
        message =
          <div>
            <h3>Here you go!</h3>
            <br/><p>A few color palettes for you to choose from.
            Click on a palette to see the colors in action!
            <br/><br/></p>
          </div>
        break;

    default :
      displayMessage = false;
      message =
        <div>
          <h3>Must have been called by accident</h3>
        </div>
        break;
  }

  useEffect(() => {
    if(displayMessage) appdispatch({type : "CLOSEHINT_CHANGE", payload : false});
  }, [appdispatch,displayMessage]);

  let backgroudStylei = {};
  backgroudStylei.position = "fixed";
  backgroudStylei.height = "var(--interact-min-height)";
  backgroudStylei.width = "var(--interact-width)";
  backgroudStylei.top = "var(--interact-fixed-top)";
  backgroudStylei.zIndex = "var(--background-z-Index)";
  backgroudStylei.backgroundColor = "var(--background-color)";
  backgroudStylei.opacity = "var(--background-opacity)";

  let backgroudStyleii = {};
  backgroudStyleii.position = "fixed";
  backgroudStyleii.height = "var(--interact-min-height)";
  backgroudStyleii.width = "var(--interact-width)";
  backgroudStyleii.top = "var(--interact-fixed-top)";
  backgroudStyleii.zIndex = "var(--background-z-Index)";
  backgroudStyleii.display = "flex";
  backgroudStyleii.justifyContent = "center";
  backgroudStyleii.alignItems = "center";

  let textwrapperStyle = {};
  textwrapperStyle.maxWidth = "var(--message-wrapper-width)";
  textwrapperStyle.backgroundColor = "var(--message-wrapper-bg-color)";
  textwrapperStyle.opacity = "var(--message-wrapper-opacity)";
  textwrapperStyle.borderRadius = "var(--message-wrapper-border-radius)";
  textwrapperStyle.display= "flex";
  textwrapperStyle.flexDirection= "column";
  textwrapperStyle.boxShadow= "var(--menu-button-shadow-xy) var(--menu-button-shadow-xy) var(--menu-button-shadow-merge) var(--menu-button-shadow-start) var(--menu-button-shadow-color)";

  let textStyle = {};
  textStyle.padding = "var(--message-padding)";


  let listStyle = {};
  listStyle.listStyleType = "disc";
  listStyle.listStylePosition = "inside";

  let checkBox=false;

  function handleClick () {
    appdispatch({type : "CLOSEHINT_CHANGE", payload :  true});
    if (checkBox) appdispatch({type : "SHOWHINT_CHANGE", payload :  false});
  }

  function handleChange () {
    checkBox=!checkBox;
  }

  let buttoncloseStyle = {};
  buttoncloseStyle.cursor = "pointer";
  buttoncloseStyle.paddingRight = "var(--close-x-button-padding-right)";
  buttoncloseStyle.alignSelf = "flex-end";

  let bigButtonStyle = {};
  bigButtonStyle.alignSelf = "center";
  bigButtonStyle.paddingBottom = "var(--close-big-button-padding-bottom)";

  return (
    <div>
    { !appstate.closeHint &&
      <div>
        <section style={backgroudStylei} className="ukcntapp">
        </section>
        <section style={backgroudStyleii} className="ukcntapp">
          <section style={textwrapperStyle} className="ukcntapp textwrapper">
            <div style={buttoncloseStyle} onClick = {handleClick}>
              <h4>x</h4>
            </div>
            <article style={textStyle} className="ukcntapp">
              {message}
              <FormControlLabel
                control={<Checkbox onChange={handleChange} />}
                label=<p>Hide all messages</p>
                labelPlacement="end"
              />
            </article>
            <div style={bigButtonStyle}>
            <Button size="large" variant="outlined" onClick = {handleClick}>
            <p>Close Message</p>
            </Button>
            </div>
          </section>
        </section>
      </div>}
    </div>
  );
}

export default BrandMessage;
