import React from 'react';
import './par_layeredapp_all.css';
import './LayeredApp.css';

//Context for the entire App
import { useAppContext } from './App/AppContext';

//viewport for screen size
import Viewport from './App/Viewport';

//router for menu
import { BrowserRouter as Router } from 'react-router-dom';

//importing menu
import Menu from './App/Menu';
import MenuRoutes from './App/MenuRoutes';
import MenuIcon from './App/MenuIcon';

//the main app starts here
const LayeredApp = () => {
  //uk - here we are fetching the current context
  //uk - dispatch is the consumer, use it to call reducer in useContext
  //uk - state is the current state of context
  const { appstate } = useAppContext();

  //set the value of user screen dimentions in context
  Viewport();

  return (
    <section className="ukcntint appwrapper" id="appwrapper1">
    { !appstate.openMenu &&
      <MenuIcon />}
      <Router>
        { appstate.openMenu &&
          <Menu />
        }
        <MenuRoutes />
      </Router>
    </section>

  );
}

export default LayeredApp;
