import React from 'react';
import './MenuCard.css';
import { useAppContext } from './AppContext';

const MenuCard = (props) => {
  const { appdispatch } = useAppContext();
  var cardDet = {};

  switch (props.type) {
    case "BRAND" :
      cardDet.name = "Brand";
      cardDet.text = "The home of your brand";
      cardDet.altname = "PERSONALITY";
      break;

    case "POSTS" :
      cardDet.name = "Posts";
      cardDet.text = "Create impressive social posts";
      cardDet.altname = "IMAGES";
      break;

    case "VIDEOS" :
      cardDet.name = "Videos";
      cardDet.text = "Animate your world";
      cardDet.altname = "ANIMATION";
      break;

    default :
      console.log("Provide a correct value for React component MenuCard type")
  }

  const handleMenuClick = () => {
    appdispatch({type : "OPENMENU_CHANGE", payload : false});
  }

  return (
    <section className="menuitem ukcntapp" id="menuitem1" onClick={handleMenuClick}>
        <section className="menudesign1 ukcntapp" id="menuitem2">
          <p>{cardDet.altname}</p>
        </section>
        <section className="menudesign4 ukcntapp" id="menuitem4">
          <section className="menudesign2 ukcntapp" id="menuitem3">
            <p>{cardDet.name}</p>
          </section>
          <section className="menudesign3 ukcntapp" id="menuitem3">
            <p>{cardDet.text}</p>
          </section>
        </section>
    </section>
  );
}

export default MenuCard;
